.simple-cards-grid {
    display: grid;
    // grid-template-columns: repeat(auto-fit, minmax(180px, 300px));
    grid-gap: 30px;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    cursor: pointer;

    >:hover {
        box-shadow: 0 0 .25rem rgba(65, 65, 65, 0.5), -.125rem -.125rem 1rem rgba(212, 212, 212), .125rem .125rem 1rem rgba(212, 212, 212);
    }

    max-width: 460px;
    margin-inline-start: auto;
    margin-inline-end: auto;

    @include for-narrow-layout {
        max-width: 700px;
    }

    @include for-normal-layout {
        max-width: 1000px;
    }

    @include for-wide-layout {
        max-width: 1400px;
    }

}

.toy-preview {
    a {
        text-decoration: none;
    }

    .toy-preview-price {
        font-size: .8rem;
    }
}


