.main-layout {
    display: grid;
    grid-template-columns: $layoutPadding 1fr $layoutPadding;

    >* {
        grid-column: 2;
    }

    >*.full {
        grid-column: 1 / -1;
    }

    >*:not(.full) {
        display: grid;
        grid-auto-flow: dense;
        grid-template-columns: repeat(6, 1fr);
    }

    @include for-narrow-layout {
        @include layoutGTC($break-narrow);
    }

    @include for-normal-layout {
        @include layoutGTC($break-normal);
    }

    @include for-wide-layout {
        @include layoutGTC($break-wide);
    }
}

.app-header {
    display: flex;
    background: linear-gradient(to bottom right, #7487ff, #b6f71d);
    justify-content: space-between;
    nav{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    a{
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        height: 130px;
        width: 130px;
    }
    
    @include for-narrow-layout {
        @include layoutGTC($break-narrow);
    }

    @include for-normal-layout {
        @include layoutGTC($break-normal);
    }

    @include for-wide-layout {
        @include layoutGTC($break-wide);
    }
}

.app-header a:hover,.app-header a.active {
    box-shadow: 0 0 .25rem rgba(212, 212, 212);
}
.app-header a {
    color: $clr3;
}


.main-text {
    margin-bottom: rem(90px);
}